import React from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import { selectVisitor } from './../actions';

const visitorsList = (visitors, selectVisitor) => {
  const list = visitors.map((x, index) => (
    <button type="button" key={index} className="list-group-item list-group-item-action" onClick={() => selectVisitor(x)}>
      {`${x.name} - ${x.country} - ${moment(x.birthdate).format('DD/MM/YY')}`}
    </button>
  ));

  return (
    <div className="list-group">
      {list}
    </div>
  );
};

const emtyVisitors = () => {
  return (
    <div className="card-body">
     <p className="text-center">No hay visitantes registrados</p>
    </div>
  );
};

const VisitorsComponent = ({ visitors, selectVisitor }) => {
  return (
    <div className="card mb-2">
      <div className="card-header">
        Visitantes anteriores
      </div>
      { visitors.length === 0 ? emtyVisitors() : visitorsList(visitors, selectVisitor) }
    </div>
  );
}

const mapStateToProps = ({ visitors }) => {
  console.log(visitors);
  return {visitors: visitors.data }
};

const Visitors = connect(mapStateToProps, { selectVisitor })(VisitorsComponent);

export { Visitors };



