import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Message } from './';
import { fetchCountries, addVisitor } from './../actions';

class FormContainer extends Component {
  state = {
    name: '',
    birthdate: '',
    country: '',
  }

  componentDidMount() {
    this.props.fetchCountries().then(() => {
      this.setState({ country: this.props.countries[0].name || '' });
    });
  }

  handleChange({ prop, value }) {
    this.setState({ [prop]: value });
  }

  hanleSubmit (event) {
    this.props.addVisitor(this.state);
    this.setState({
      name: '',
      birthdate: '',
      country: this.props.countries[0].name || ''
    });
    event.preventDefault();
  }

  renderCountriesOptions() {
    const { countries } = this.props;
    return countries.map(country => (
      <option key={country.alpha3Code}>{country.name}</option>
    ));
  }

  render() {
    const { name, birthdate, country } = this.state;

    return (
      <div className="card mb-2">
        <div className="card-header">Formulario</div>
        <div className="card-body">
          <form onSubmit={(event) => { this.hanleSubmit(event);}}>
            <div className="form-group">
              <label htmlFor="exampleFormControlInput1">Nombre</label>
              <input
                type="text"
                className="form-control"
                placeholder="John Doe"
                value={name}
                required
                onChange={(event) => {
                  this.handleChange({ prop: 'name', value: event.target.value });
                }}
              />
            </div>
            <div className="form-group">
              <label htmlFor="exampleFormControlSelect1">Pais</label>
              <select
                className="form-control"
                value={country}
                required
                onChange={(event) => {
                  this.handleChange({ prop: 'country', value: event.target.value });
                }}
              >
                {this.renderCountriesOptions()}
              </select>
            </div>
            <div className="form-group">
              <label htmlFor="exampleFormControlInput1">Fecha de Nacimiento</label>
              <input
                type="date"
                required
                className="form-control"
                placeholder="25"
                value={birthdate}
                onChange={(event) => {
                  this.handleChange({ prop: 'birthdate', value: event.target.value });
                }}
              />
            </div>
            <button type="submit" className="btn btn-primary btn-md btn-block">Saludar</button>
          </form>
          <Message />
        </div>
      </div>
    );
  }
}

const mapStateToProps = ({ countries }) => ({ countries });

const Form = connect(mapStateToProps, { fetchCountries, addVisitor })(FormContainer);

export { Form };



