import React from 'react';
import { connect } from 'react-redux';
import moment from 'moment';
import 'moment/locale/es';

moment.locale('es');

const MessageComponent = ({ lastVisitor = null }) => {
  if (! lastVisitor) {
    return <div></div>;
  }

  const { birthdate, name, country } = lastVisitor;
  const birthdateMoment = moment(birthdate);
  const years = moment().diff(birthdate, 'years',false);
  return(
    <div className="alert alert-primary mt-3" role="alert">
      {
        `Hola ${name} de ${country}, el ${birthdateMoment.date()} de
        ${birthdateMoment.format('MMMM')} tendras ${years + 1} ${years + 1 === 1 ? 'año' : 'años'}`
      }
    </div>
  );
};

const mapStateToProps = ({ visitors }) => ({
  lastVisitor: visitors.selected,
});

const Message = connect(mapStateToProps)(MessageComponent);

export { Message };



