import { ADD_VISITOR } from '../actions';
import { SELECT_VISITOR } from '../actions';

const initialState = {
  data: [],
  selected: null,
};

export default function(state = initialState, action) {
  switch (action.type) {
    case ADD_VISITOR:
      return { data: [ ...state.data, action.payload], selected: action.payload};

    case SELECT_VISITOR:
      return { ...state, selected: action.payload };

    default:
      return state;
  }
}



